import React from "react";
import "./style.css";
import "./drag.js";
import "./friend_gen.js";
import generateGroup from "./friend_gen.js";

function getRandomX() {
  const max = (window.innerWidth || document.body.clientWidth) - 300;
  const min = 0;
  const pos = Math.random() * (max - min) + min;
  console.log(pos);
  return pos;
}

function getRandomY() {
  const max = window.innerHeight || document.body.clientHeight;
  const min = 0;
  return (Math.random() * (max - min) + min) / 2;
}

function Window({
  windowWidth = "300px",
  focussed,
  title = "Untitled",
  startPos,
  type,
  children,
  mouseDownHandler,
  clickHandler,
  ...rest
}) {
  var focusClass = "unfocussed";
  if (focussed === "true") {
    focusClass = "focussed";
  }
  if (type === "notepad") {
    return (
      <div
        className="window"
        data-x="100"
        onMouseDown={mouseDownHandler}
        onClick={clickHandler}
        style={{ left: `${startPos.x}px`, top: `${startPos.y}px` }}
      >
        <TitleBar barWidth={windowWidth}>Notepad</TitleBar>
        <Box className={focusClass} boxWidth={windowWidth} style={{}}>
          <div
            style={{
              height: "calc(100% - 6px)",
              marginLeft: "3px",
              marginTop: "2px",
              marginRight: "3px"
            }}
          >
            <textarea
              className="notepad"
              style={{
                height: "100%",
                width: "100%",
                border: "none",
                outline: "none"
              }}
            />
          </div>
        </Box>
      </div>
    );
  }

  // if (type === "friend") {
  //   return (
  //     <canvas
  //       className="friend-drag"
  //       id="polygon_canvas"
  //       width="280px"
  //       height="280px"
  //     />
  //   );
  // }

  if (type === "welcome") {
    return (
      <div
        className="window"
        data-x="100"
        onMouseDown={mouseDownHandler}
        onClick={clickHandler}
        style={{ left: `${startPos.x}px`, top: `${startPos.y}px` }}
      >
        <TitleBar barWidth={windowWidth}>Welcome!</TitleBar>
        <Box className={focusClass} boxWidth={windowWidth} style={{}}>
          <canvas id="polygon_canvas" width="280px" height="280px" />
          <button
            type="button"
            className="btn btn-primary"
            onClick={() => generateGroup()}
          >
            Create Friend
          </button>
        </Box>
      </div>
    );
  }

  return (
    <div
      className="window"
      onMouseDown={mouseDownHandler}
      onClick={clickHandler}
    >
      <TitleBar barWidth={windowWidth}>{title}</TitleBar>
      <Box
        className={focusClass}
        boxWidth={windowWidth}
        style={{ backgroundColor: "lightblue" }}
      >
        {children}
      </Box>
    </div>
  );
}

function Desktop(numWindows = 3, ...rest) {
  const [windowArr, setWindowArr] = React.useState([
    {
      id: "_" + Math.random().toString(36).substr(2, 9),
      type: "welcome",
      startPos: { x: `${getRandomX()}`, y: `${getRandomY()}` }
    }
  ]);
  function reorderArr(win) {
    let tempWinArr = currentWinArr.slice();
    let focussedWin = tempWinArr.splice(tempWinArr.indexOf(win), 1);
    let newArray = tempWinArr.concat(focussedWin);
    setWindowArr(newArray);
  }
  const currentWinArr = windowArr.slice();

  const getID = () => "_" + Math.random().toString(36).substr(2, 9);

  const addWindow = (type) =>
    setWindowArr(
      currentWinArr.concat({
        id: getID(),
        type: type,
        startPos: { x: `${getRandomX()}`, y: `${getRandomY()}` }
      })
    );

  function removeWindow(win) {
    var newArray = currentWinArr.slice();
    newArray.splice(currentWinArr.indexOf(win), 1);
    setWindowArr(newArray);
  }

  function handleWindowClick(e, win) {
    if (e.target.classList.contains("exit-icon")) {
      removeWindow(win);
      console.log("removing");
      return;
    }
  }

  function handlemouseDown(win) {
    //todo: trigger window close on 'onclick' but reorder on 'mousedown'
    reorderArr(win);
  }

  function renderWindows() {
    //todo: refactor focus handling
    return (
      <div
        className="desktop"
        style={{
          pointerEvents: "none",
          position: "fixed",
          width: "100%",
          height: "95%"
        }}
      >
        {windowArr.map((winObj, index) =>
          index === windowArr.length - 1 ? (
            <Window
              focussed="false"
              clickHandler={() => handleWindowClick(winObj)}
              mouseDownHandler={() => handlemouseDown(winObj)}
              key={winObj.id}
              type={winObj.type}
              startPos={winObj.startPos}
            />
          ) : (
            <Window
              focussed="true"
              clickHandler={() => handleWindowClick(winObj)}
              mouseDownHandler={() => handlemouseDown(winObj)}
              key={winObj.id}
              type={winObj.type}
              startPos={winObj.startPos}
            />
          )
        )}
      </div>
    );
  }
  return (
    <div>
      <div className="topBar">
        <Clock />
      </div>
      <Icon
        clickHandler={() => addWindow("notepad")}
        image={require("./notepad.png")}
        alt="notepad icon"
        style={{
          top: "300px"
        }}
        size="75"
      >
        Notepad
      </Icon>
      {renderWindows()}
    </div>
  );
}

function Clock() {
  const [datetime, setDatetime] = React.useState(getTime());

  function getTime() {
    const date = new Date().toLocaleDateString();
    const time = new Date().toLocaleTimeString();
    return `${date} - ${time}`;
  }

  React.useEffect(() => {
    var timerID = setInterval(() => setDatetime(getTime()), 1000);

    return function cleanup() {
      clearInterval(timerID);
    };
  }, [datetime]);

  return datetime;
}

function Icon({
  image,
  alt,
  clickHandler = "",
  className = "icon",
  size,
  style,
  children,
  ...rest
}) {
  console.log(children);
  return (
    <div style={style} className={className} onClick={clickHandler}>
      <img type="image" src={image} alt={alt} width={size} height={size} />
      <div>{children}</div>
    </div>
  );
}

function Box({ className = "", style, boxWidth, ...rest }) {
  return (
    <div
      className={`box resizable ${className}`}
      style={{ width: `${boxWidth}`, height: `${boxWidth}`, ...style }}
      {...rest}
    />
  );
}
function TitleBar({ className = "", barWidth, children, ...rest }) {
  return (
    <div className={`titlebar draggable ${className}`} {...rest}>
      {children}
      <div style={{ float: "right", marginRight: "0px" }}>
        <p className="exit-icon">X</p>
      </div>
    </div>
  );
}

export default Desktop;
